import { TablePlugin } from '@ali/4ever-bamboo';
import { Table } from '@ali/4ever-mo';
import { createControllerInstance, createTableInstance, getTableFromController } from '../helpers';

const { enableAutofitWidth } = TablePlugin.commands;

describe('bi/table/commands/enableAutofitWidth', () => {
  it('normal case', () => {
    const table = createTableInstance();
    const controller = createControllerInstance([table]);

    const originTable = getTableFromController(controller);
    expect(Table.isAutofitWidth(originTable)).toBe(false);

    enableAutofitWidth(controller, originTable, true);
    const autofitTable = getTableFromController(controller);
    expect(Table.isAutofitWidth(autofitTable)).toBe(true);

    enableAutofitWidth(controller, autofitTable, false);
    const notAutofitTable = getTableFromController(controller);
    expect(Table.isAutofitWidth(notAutofitTable)).toBe(false);
  });
});
